<template>
  <div class="rules page">
    <div class="scroll_content_box">
      <item v-for="item in rules" :key="item.id" :ruleItem="item"></item>
      <no-item v-if="rules.length===0"></no-item>
      <div class="blank"></div>
    </div>
  </div>
</template>

<script>
import Item from './Item'
export default {
  name: 'Rule',
  components: {
    Item
  },
  data () {
    return {
      rules: [
        {
          id: 1,
          rules_id: 'sn123456789011',
          profession_kind: '级别分类',
          url: 'http://p7s4hdszs.bkt.clouddn.com/FuVJWAvVVw1EBORTJDhS2CvXQdeY'
        }
      ]
    }
  },
  methods: {
    getRuleData () {
      this.$rpc.call('xian_metro.xian_metro', 'get_xian_metro_list', [], {}).then(res => {
        this.rules = res
      }).catch(e => {
        console.log(e)
        alert('获取规章制度失败')
      })
    }
  },
  mounted () {
    this.getRuleData()
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/common';
.rules{
  .scroll_content_box{
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
    height: 100%;
  }
}
</style>
